@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.identity-card {
  background-color: #FFFCF9;
  width: 64rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.profile-pic {
  width: 32rem;
  border-radius: 50%;
}

.name {
  font-size: 7.2rem;
  color: #373F51;
}

.course-details {
  height: 9.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.student-year {
  color: #8B8E98;
  font-size: 2.8rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.course-name {
  font-size: 2.4rem;
  color: #8B8E98;
  font-feature-settings: "smcp";
  font-variant: small-caps;
}

.social-links {
  height: 18rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-evenly;
}

.social-link {
  display: flex;
  align-items: center;
}

.social-link a {
  color: #56A3A6;
  font-size: 3.2rem;
  text-decoration: none;
}

.social-link a:hover {
  text-decoration: underline;
}

.social-link img {
  height: 6.4rem;
  width: 6.4rem;
  margin-left: 1.5rem;
}
.list h3 {
  font-size: 3.6rem;
  font-weight: 700;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  margin-bottom: 1.8rem;
}

.list ul {
  list-style: none;
}

.list ul li {
  margin-bottom: 0.8rem;
}

.list ul li::before {
  content: "- ";
}
.content-section {
  margin: 0 auto;
  width: 80%;
}

.section-heading {
  color: #67C0C4;
  font-size: 5.6rem;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 2.8rem;
  margin-top: 6.4rem;
}

.skill-lists {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

.project-links {
  display: flex;
  flex-direction: column;
}

.project-links a {
  color: #67C0C4;
  padding-right: 2rem;
  text-decoration: none;
}

.project-links a:hover {
  text-decoration: underline;
}

.project-listing {
  margin-bottom: 1rem;
}
.app {
  min-height: 100vh;
  background-color: #373F51;
  display: grid;
  grid-template-columns: -webkit-min-content 1fr;
  grid-template-columns: min-content 1fr;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 2.8rem;
  color: #FFF;
}
